import React from 'react';
import ProfileInfo from "./ProfileInfo/ProfileInfo";
import MyPostsContainer from "./MyPosts/MyPostsContainer";
import Profile from "./Profile";
import axios from "axios";
import {connect} from "react-redux";
import {getUserProfile, getUserStatus, setUserProfile, updateUserStatus} from "../../redux/profile-reducer";



import {useNavigate, useParams} from "react-router-dom";

import {compose} from "redux";
import {withAuthRedirect} from "../../hoc/withAuthRedirect";




export function withRouter(Children) {
	return (props) => {
		const match = {params: useParams()};
		const navigate = useNavigate();
		return <Children {...props} match={match} navigate={navigate}/>
	}
}


class ProfileContainer extends React.Component {
	/*let postsDataFromApp = props.postsDataToProfile;*/


	componentDidMount() {

		let userId = this.props.match.params.userId;
		console.log(this.props)

		if (!userId) {
			userId = this.props.authorizedUserId;
			if (!userId) {
				this.props.navigate('./login')
			}

		}

		this.props.getUserProfile(userId);
		this.props.getUserStatus(userId);

		/*usersAPI.getProfileAPI(userId)
			.then(data => {

				this.props.setUserProfile(data);

			});*/
	}


	render() {
		return (
			<Profile {...this.props} />
		);
	}


}

let mapStateToProps = (state) => ({
	profile: state.profilePage.profile,
	status: state.profilePage.status,
	authorizedUserId: state.auth.userId,
})


/*let WithUrlDataContainerComponent = withRouter(ProfileContainer);*/


/*export default connect(mapStateToProps, {getUserProfile})(WithUrlDataContainerComponent);*/

export default compose(
	connect(mapStateToProps, {getUserProfile, getUserStatus, updateUserStatus}),
	withRouter

)
(ProfileContainer)